.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

h1 {
  text-align: center;
  font-size: 50px;
  font-family: fantasy;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;

  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;

}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.btn{
  justify-self: center;
  text-align: center;
  margin-top: 15px;
  
}


.movieStyleDiv{
  background-color: rgba(18, 66, 139, 0.966);;
  min-width: 80%;
  height: 100%;
  padding: 6%;
  color: rgb(201, 201, 209);
  font-weight: bold;
}

button{
  margin: 20px;
  align-items: center;
  border-radius: 30px 20px 30px 10px;
  color: navy;
  font-weight: bold;
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 16px;
  box-shadow: rgba(45, 51, 51, 0.363) 20px 0px 20px 0px;
}

button img {
  display: flex;
  width: 200px;
  margin: 30px;
}

.displayDiv {
  margin: 10% 35%;
  background-color: rgb(163, 68, 45);
  padding: 30px;
  width: 300px;
  font-size:20px;
  font-family: fantasy;
  text-align: center;
  border-radius: 20px 30px;

}